import * as React from 'react';
import Grid2 from '@mui/material/Unstable_Grid2';
import Typography from '@mui/material/Typography';
import { useRef } from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
const type = [
  'ESFJ',
  'ISFJ',
  'ESTJ',
  'ISTJ',
  'ESFP',
  'ISFP',
  'ENFJ',
  'INTP',
  'ESTP',
  'ISTP',
  'ENTP',
  'INFJ',
  'ENTJ',
  'INTJ',
  'ENFP',
  'INFP',
];

const ResultLoading = ({ dataPatch, setEndLoading, type }) => {
  const ref = useRef(null);
  const [downloadImg, setDownloadImg] = useState(false);

  const handleOnEnded = () => {
    setEndLoading(true);
  };
  useEffect(() => {
    const img = new Image();
    const video = new Image();
    if (type) {
      img.src = process.env.PUBLIC_URL + `/characters/${type}.png`;
      video.src = process.env.PUBLIC_URL + `/characters/${type}.webm`;
      setDownloadImg(true);
    }
  }, [type]);
  return (
    <Grid2
      container
      rowGap={1}
      position='absolute'
      sx={{ background: 'white' }}
      zIndex='9999'
    >
      <Grid2 xs={12}>
        <Typography
          variant='h2'
          color='primary'
          align='center'
        >
          Ur MBTI
        </Typography>
      </Grid2>
      <Grid2 xs={12}>
        <video
          autoPlay
          playsInline={true}
          preload='auto'
          muted
          width='100%'
          loop={!dataPatch || !downloadImg}
          ref={ref}
          onEnded={handleOnEnded}
        >
          <source
            src={process.env.PUBLIC_URL + '/videos/loading5.webm'}
            type={'video/webm'}
          />
        </video>
      </Grid2>
    </Grid2>
  );
};

export default ResultLoading;
