import { Box, Divider, Typography } from '@mui/material';
import CharacterImage from '../../components/questions/result/CharacterImage';
import Grid2 from '@mui/material/Unstable_Grid2/Grid2';
import ResultInfo from '../../components/questions/result/ResultInfo';
import { useEffect, useState } from 'react';
import ResultLoading from './ResultLoading';
import Confetti from '../../components/features/confettis/Confetti';
import ResultAni from './ResultAni';
import { useDispatch, useSelector } from 'react-redux';
import { CallPostMBTIQuestionAPI } from '../../services/apis/mbtiAPICalls';
import { useNavigate } from 'react-router-dom';

const Result = function () {
  const testResult = useSelector((s) => s.testResultReducer).result;
  const { choices, result, username, comment } = useSelector(
    (s) => s.mbtiReducer
  );
  const type = testResult.mbti;
  const ratioResult = testResult.ratio;
  const ratio = [
    100 - ratioResult?.E,
    100 - ratioResult?.S,
    ratioResult?.T,
    100 - ratioResult?.J,
  ];
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [endloading, setEndLoading] = useState(false);
  const [dataFatch, setDataFatch] = useState(false);
  const [endAni, setEndAni] = useState(false);

  useEffect(() => {
    if (result === '') {
      alert('MBTI  !');
      return navigate('/');
    }
    const notAns = choices.filter((x) => x.value === '').length;
    if (notAns > 0) {
      alert('   .');
      return navigate('/question');
    }
    if (comment.length > 300) {
      alert('  300     .');
      return navigate('/question');
    }
    !testResult.isDone && dispatch(CallPostMBTIQuestionAPI());
    setDataFatch(true);
  }, []);

  useEffect(() => {}, []);
  return (
    <>
      <Box position='relative'>
        {!endloading && (
          <ResultLoading
            type={type}
            dataPatch={dataFatch}
            setEndLoading={setEndLoading}
          />
        )}
        <ResultAni
          endloading={endloading}
          type={type}
          handleOnEnded={setEndAni}
        />
        <Box display={endAni ? 'block' : 'none'}>
          {endAni && <Confetti />}
          <Grid2
            container
            rowGap={1}
          >
            <Grid2 xs={12}>
              <Typography
                variant='h2'
                color='primary'
                align='center'
              >
                Ur MBTI
              </Typography>
            </Grid2>
            <Grid2
              xs={12}
              display='flex'
              justifyContent='center'
              flexDirection='column'
              minHeight={400}
            >
              <CharacterImage type={type} />
            </Grid2>
            <Grid2 xs={12}>
              <ResultInfo
                type={type}
                ratio={ratio}
              />
            </Grid2>
            <Grid2 xs={12}>
              <Divider sx={{ borderStyle: 'dashed' }} />
            </Grid2>
          </Grid2>
        </Box>
      </Box>
    </>
  );
};

export default Result;
